/////////////////////
// General Mixins (put specific ones in component files where applicable)

/// Mixin - Clearfix.
/// Adds clearfix based on http://bourbon.io/docs/#clearfix
/// use example =   @include clearfix

@mixin clearfix {
  &::after {
    clear: both;
    content: '';
    display: table;
  }
}

/// Mixin - list-reset
/// Reset list item defaults when no margin, padding, list styles needed
@mixin list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

/// Mixin - With Icon
/// Precede Text with SVG icon
@mixin with-icon {
  background-size: cover;
  content: '';
  display: inline-block;
  margin: 0 0.1em 0 0;
  padding: 0.8em;
}

/// Mixin - Wrapper
/// Outer container mixin for large screens
@mixin wrapper {
  @include clearfix;

  margin: 0 auto;
  max-width: $max-width;
}

/// Mixin - More Link
/// Used in a number of views
@mixin more-link {
  a {
    font-size: 0.9rem;
    font-weight: 600;
    text-decoration: none;

    &::before {
      content: '';
      font-size: 1.2rem;
      margin-right: 0.2em;
    }
  }
}

/// Mixin - Back Link
/// Used in a number of views
@mixin back-link {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  text-decoration: none;

  &::before {
    content: '';
    font-size: 1.2rem;
    margin-right: 0.2em;
  }
}

/// Mixin - Body Copy
@mixin body-copy {
  font-family: $font-body;
  font-size: 0.9rem;
  line-height: 1.6em;
}

/// Mixin - Transition
@mixin transition($transition-property, $transition-time, $method) {
  -webkit-transition: $transition-property $transition-time $method;
  -moz-transition: $transition-property $transition-time $method;
  -ms-transition: $transition-property $transition-time $method;
  -o-transition: $transition-property $transition-time $method;
  transition: $transition-property $transition-time $method;
}

/// Mixin - Standard Margin
@mixin margin {
  margin-bottom: 1em;
}

@mixin p-reset-list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}

@mixin p-link-underline {
  @include transition(border, 0.4s, ease);

  border-bottom: 3px solid transparent;
  color: $color-link;
  padding-bottom: 1px;
  padding-top: 3px;
  text-decoration: none;

  &:hover {
    border-bottom: 3px solid $color-link;
  }
}

@mixin p-container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1320px;
  padding: 0 20px;
}
